.wrapper {
    max-width: 100%;
    height: 100%;
    padding-top: 60px;
    display: flex;
    flex-direction: column;
}

.messages {
    width: 100%;
    height: calc(100vh - 140px);
}

.optionsBox {
    display: flex;
    width: 100%;
    height: 80px;
    bottom: 0;
    align-items: center;
    overflow-x: scroll;
}

.image {
    height: 50px;
    width: 25px;
    cursor: pointer;
}

.button {
    border: none;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #5aa4fc;
    z-index: 10;
    position: absolute;
    right: 10px;
    bottom: 15px;
}

.space {
    height: 80px;
    min-width: 80px;
}